// ============================================================
// THEME CONFIG HELPER
// ============================================================

export type ThemeName = 'slate' | 'blue' | 'green' | 'amber' | 'rose' | 'purple' | 'orange' | 'teal';

const lightThemes: Record<ThemeName, CustomThemeConfig> = {
    slate: slateLight,
    blue: blueLight,
    green: greenLight,
    amber: amberLight,
    rose: roseLight,
    purple: purpleLight,
    orange: orangeLight,
    teal: tealLight,
};

const darkThemes: Record<ThemeName, CustomThemeConfig> = {
    slate: slateDark,
    blue: blueDark,
    green: greenDark,
    amber: amberDark,
    rose: roseDark,
    purple: purpleDark,
    orange: orangeDark,
    teal: tealDark,
};

// Legacy export for backward compatibility
export const themes: Record<string, CustomThemeConfig> = lightThemes;

/**
 * Get theme config for specific mode
 * @param themeName Name of the theme
 * @param mode 'light' or 'dark'
 * @returns Complete theme configuration
 */
export function getThemeConfig(themeName: string, mode: 'light' | 'dark'): CustomThemeConfig {
    const name = (themeName as ThemeName) || 'slate';
    
    if (mode === 'dark') {
        return darkThemes[name] || slateDark;
    }
    
    return lightThemes[name] || slateLight;
}
